import React, { Component } from 'react';
import { Paper, Grid, Card, CardActionArea, Chip } from '@material-ui/core';
import { Link } from 'react-router-dom';
import ContentService from '../../services/ContentService';
import '../../App.css';

class Home extends Component {
    state = {
        data: []
    }

    componentDidMount() {
        ContentService.getProducts().then(res => {
            this.setState({ data: res.data });
        });
    }

    getOffers() {
        const { data } = this.state;
        return data.filter(product => {
            if (product.offer) {
                return true;
            }
            else {
                return false;
            }
        });
    }

    render() {
        const offers = this.getOffers();
        return (
            <div>
                <Grid Grid container spacing={3} justify="center" alignItems="center">
                    <Grid item xs={12}>
                        <h1 style={{ fontSize: '3rem' }}>Welcome to <span style={{ fontFamily: 'Atiba' }}>eCOM</span></h1>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)', padding: '5px' }}>
                            <Paper elevation={5} style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)', margin: '10px', padding: '3px' }}>
                                <Paper elevation={5} style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)', margin: '10px', padding: '2px' }}>
                                    <Paper elevation={0} style={{ margin: '10px', backgroundColor: '#0e80a9' }}>
                                        <ul style={{ textAlign: 'center', marginLeft: '-2.7vh', padding: '50px', color: 'white' }}>
                                            <h3>New Offers</h3>
                                            {offers.length ? offers.map(product =>
                                                <Link to={{
                                                    pathname: `/products/${product.name}`,
                                                    state: {
                                                        productName: product.name
                                                    }
                                                }}>
                                                    <Card key={product.name} style={{ display: 'inline-block', margin: '10px', backgroundColor: '#e3b035' }}>
                                                        <CardActionArea>
                                                            <img src={product.image}
                                                                width="150"
                                                                height="150"
                                                                alt={product.name}
                                                            />
                                                            <div style={{ paddingTop: '5px' }}>
                                                                <p><Chip label={product.offer} color="secondary" /></p>
                                                                <p style={{ fontSize: '14px', marginTop: '-2vh' }}>{product.name}</p>
                                                            </div>
                                                        </CardActionArea>
                                                    </Card>
                                                </Link>
                                            ) : null}
                                        </ul>
                                    </Paper>
                                </Paper>
                            </Paper>
                        </Paper>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default Home;