import React, { Component } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';
import { Chip, Grid, Paper, Breadcrumbs, DialogTitle, DialogActions } from '@material-ui/core';
import { Link } from 'react-router-dom';
import Pagination from '@material-ui/lab/Pagination';
import Dialog from '@material-ui/core/Dialog';
import ContentService from '../../services/ContentService';
import { Helmet } from 'react-helmet';

class Products extends Component {
    state = {
        data: [],
        cart: [],
        includeCart: false,
        productExists: false,
        page: 1,
        size: 5,
        start: 0,
        end: 5
    }

    componentDidMount() {
        ContentService.getProducts().then(res => {
            this.setState({ data: res.data });
        });
        if (localStorage.getItem("cart")) {
            this.setState({
                cart: JSON.parse(localStorage.getItem("cart"))
            });
        }
    }

    useStyles() {
        return makeStyles({
            root: {
                maxWidth: 345,
                backgroundColor: red,
            },
            media: {
                height: 0,
                paddingTop: '56.25%',
            },
            paper: {

            }
        });
    }

    checkCart(product) {
        const { cart } = this.state;
        if (cart.length) {
            this.setState({ includeCart: true });
            localStorage.setItem("selectedProduct", JSON.stringify(product));
        }
        else {
            const cart = [product];
            this.setState({ cart: cart });
            localStorage.setItem("cart", JSON.stringify(cart));
            this.props.history.push("/checkout");
        }
    }
    contains(cart, product) {
        for (let i = 0; i < cart.length; i++) {
            if (cart[i].name === product.name) {
                return true;
            }
        }
        return false;
    }
    findProduct(cart, product) {
        for (let i = 0; i < cart.length; i++) {
            if (cart[i].name === product.name) {
                return i;
            }
        }
        return null;
    }
    addToCart(product) {
        this.setState({ productExists: false });
        if (this.state.cart === null) {
            const cart = [product];
            this.setState({ cart: cart });
            localStorage.setItem("cart", JSON.stringify(cart));
        }
        else if (!this.contains(this.state.cart, product)) {
            const cart = [...this.state.cart, product];
            this.setState({ cart: cart });
            localStorage.setItem("cart", JSON.stringify(cart));
        }
        else {
            this.setState({ productExists: true });
        }
    }

    delete(product) {
        const { cart } = this.state;
        cart.splice(this.findProduct(cart, product), 1);
        this.setState({ cart: cart });
        localStorage.setItem("cart", JSON.stringify(cart));
    }

    buyAll() {
        const { cart } = this.state;
        const selectedProduct = JSON.parse(localStorage.getItem("selectedProduct"));
        if (!this.contains(cart, selectedProduct)) {
            const cart = [...this.state.cart, selectedProduct];
            this.setState({ cart: cart });
            localStorage.setItem("cart", JSON.stringify(cart));
            localStorage.removeItem("selectedProduct");
        }
        this.props.history.push("/checkout");
    }

    buy() {
        const selectedProduct = JSON.parse(localStorage.getItem("selectedProduct"));
        const cart = [selectedProduct];
        this.setState({ cart: cart });
        localStorage.setItem("cart", JSON.stringify(cart));
        localStorage.removeItem("selectedProduct");
        this.props.history.push("/checkout");
    }
    handlePageChange = (event, value) => {
        const { size } = this.state;
        this.setState({ start: size * (value - 1), end: size * value });
    }

    handleClose = (event) => {
        this.setState({ productExists: false });
    }

    render() {
        const classes = this.useStyles();
        const { data } = this.state;
        const { cart } = this.state;
        const { includeCart } = this.state;
        const { productExists } = this.state;
        const { size } = this.state;
        const count = Math.ceil(data.length / size);
        const { start } = this.state;
        const { end } = this.state;
        const { page } = this.state;

        return (
            <div>
                <Helmet>
                    <title>Products</title>
                </Helmet>
                <Dialog open={includeCart} onClose={this.handleClose}>
                    <DialogTitle>
                        Add items in cart?
                    </DialogTitle>
                    <DialogActions>
                        <Button variant="contained" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)' }} size="small" onClick={() => this.buyAll()}>
                            YES
                        </Button>
                        <Button color="secondary" variant="contained" size="small" onClick={() => this.buy()}>
                            NO
                        </Button>
                    </DialogActions>
                </Dialog>
                <Dialog open={productExists}>
                    <DialogTitle>
                        Product is already in cart
                    </DialogTitle>
                    <DialogActions>
                        <Button onClick={this.handleClose} variant="contained" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)' }}>
                            OK
                        </Button>
                    </DialogActions>
                </Dialog>
                <Grid container spacing={3} justify="center" style={{ marginTop: '1vh', width: '99vw' }}>
                    <Grid item xs={12} sm={8}>
                        <Paper style={{ backgroundColor: '#0e80a9' }}>
                            <Breadcrumbs aria-label="breadcrumb" style={{ padding: '20px' }}>
                                <Link style={{ color: 'white' }} to="/">
                                    Home
                            </Link>
                                <Typography style={{ color: 'lightgrey' }}>Products</Typography>
                            </Breadcrumbs>
                        </Paper>
                        <Paper className={classes.paper} style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)' }}>
                            <h3 style={{ color: 'white', paddingTop: '10px' }}>Products</h3>
                            <hr style={{ width: '90%', borderWidth: '2px' }} />
                            <ul style={{ textAlign: 'center', marginLeft: '-7vh', paddingTop: '20px', paddingBottom: '20px' }}>
                                {data && data.length ? data.slice(start, end).map((product) =>
                                    <li style={{ display: 'inline-block', color: 'black', marginLeft: '10px', marginBottom: '10px' }} key={product.name}>
                                        <Card className={classes.root} elevation={5}>
                                            <Link to={{
                                                pathname: `/products/${product.name}`,
                                                state: {
                                                    productName: product.name
                                                }
                                            }}>
                                                <CardActionArea>
                                                    <CardContent>
                                                        <img src={product.image} height="200" width="200" alt={product.name} />
                                                        <Typography gutterBottom variant="h5" component="h2">
                                                            {product.name}
                                                        </Typography>
                                                        <Typography variant="body2" color="textSecondary" component="p">
                                                            {product.detail}
                                                        </Typography>
                                                    </CardContent>
                                                </CardActionArea>
                                            </Link>
                                            <CardActions style={{ marginLeft: '8%' }}>
                                                <Button variant="contained" size="small" style={{ backgroundColor: '#0e80a9', color: 'white' }} onClick={() => this.checkCart(product)}>
                                                    Buy
                                                </Button>
                                                <Button variant="outlined" size="small" color="primary" style={{ borderColor: '#FF8E53', color: '#FF8E53' }} onClick={() => this.addToCart(product)}>
                                                    Add to cart
                                                </Button>
                                            </CardActions>
                                        </Card>
                                    </li>) : null}
                            </ul>
                        </Paper>
                        {data && data.length ? <Paper style={{ marginTop: '-2.2vh', padding: '10px', backgroundColor: '#0e80a9' }}>
                            <div style={{ alignItems: 'center', display: 'inline-block', color: 'white' }}>
                                <Pagination count={count} variant="outlined" page={page} onChange={this.handlePageChange} />
                            </div>
                        </Paper> : null}
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        {cart && cart.length ?
                            <Paper className={classes.paper} style={{ backgroundColor: "#0e80a9", color: 'white', textAlign: 'center' }}>
                                <div style={{ padding: '20px' }}>
                                    <h3>Cart</h3>
                                    <hr />
                                    <div style={{ margin: '10px' }}>
                                        {cart.length ? cart.map((product) =>
                                            <Chip key={product.name}
                                                label={product.name}
                                                onDelete={() => this.delete(product)}
                                                style={{ marginRight: '10px', backgroundColor: '#FF8E53', color: 'white' }}
                                            />
                                        ) : null}
                                    </div>
                                    <Link to="/checkout" style={{ textDecoration: 'none' }}>
                                        <Button variant="contained" size="small" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)', color: 'white' }}>
                                            Checkout
                                        </Button>
                                    </Link>
                                </div>
                            </Paper> : null}
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default Products;