import React, { Component } from 'react';
import { Typography, Chip, Button, Grid, Paper, Breadcrumbs, Dialog, DialogActions, DialogTitle } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import { Link } from 'react-router-dom';
import ContentService from '../../services/ContentService';
import { Helmet } from 'react-helmet';

class Details extends Component {
    state = {
        data: [],
        product: {},
        cart: [],
        includeCart: false,
        productExists: false
    }
    useStyles() {
        return makeStyles({
            root: {
                display: 'block',
                width: '100%',
                minHeight: '100vh'
            },
            media: {
                height: 0,
                paddingTop: '56.25%',
            },
        });
    }
    checkCart(product) {
        const { cart } = this.state;
        if (cart.length) {
            this.setState({ includeCart: true });
            localStorage.setItem("selectedProduct", JSON.stringify(product));
        }
        else {
            const cart = [product];
            this.setState({ cart: cart });
            localStorage.setItem("cart", JSON.stringify(cart));
            this.props.history.push("/checkout");
        }
    }

    contains(cart, product) {
        if (cart !== null) {
            for (let i = 0; i < cart.length; i++) {
                if (cart[i].name === product.name) {
                    return true;
                }
            }
        }
        return false;
    }
    findProductIndex(cart, product) {
        for (let i = 0; i < cart.length; i++) {
            if (cart[i].name === product.name) {
                return i;
            }
        }
        return null;
    }
    addToCart(product) {
        this.setState({ productExists: false });
        if (this.state.cart === null) {
            const cart = [product];
            this.setState({ cart: cart });
            localStorage.setItem("cart", JSON.stringify(cart));
        }
        else if (!this.contains(this.state.cart, product)) {
            const cart = [...this.state.cart, product];
            this.setState({ cart: cart });
            localStorage.setItem("cart", JSON.stringify(cart));
            this.props.history.push("/products");
        }
        else {
            this.setState({ productExists: true });
        }
    }

    delete(product) {
        const { cart } = this.state;
        cart.splice(this.findProduct(cart, product), 1);
        this.setState({ cart: cart });
        localStorage.setItem("cart", JSON.stringify(cart));
    }

    buyAll() {
        const selectedProduct = JSON.parse(localStorage.getItem("selectedProduct"));
        const cart = [...this.state.cart, selectedProduct];
        this.setState({ cart: cart });
        localStorage.setItem("cart", JSON.stringify(cart));
        this.props.history.push("/checkout");
    }

    buy() {
        const selectedProduct = JSON.parse(localStorage.getItem("selectedProduct"));
        const cart = [selectedProduct];
        this.setState({ cart: cart });
        localStorage.setItem("cart", JSON.stringify(cart));
        this.props.history.push("/checkout");
    }
    async findProduct(productName) {
        const { data } = this.state;
        for (let i = 0; i < data.length; i++) {
            if (data[i].name === productName) {
                return data[i];
            }
        }
        return null;
    }

    componentDidMount() {
        ContentService.getProducts().then(res => {
            this.setState({ data: res.data });
            const productName = this.props.location.pathname.split('/')[2];
            this.findProduct(productName).then(product => {
                this.setState({ product: product });
            });
            if (localStorage.getItem("cart")) {
                const cart = JSON.parse(localStorage.getItem("cart"));
                this.setState({ cart: cart });
            }
        });
    }

    handleClose = (event) => {
        this.setState({ productExists: false });
    }

    render() {
        const { product } = this.state;
        const { cart } = this.state;
        const { includeCart } = this.state;
        const { productExists } = this.state;
        const classes = this.useStyles();
        return (
            <div>
                <Helmet>
                    <title>{this.props.location.pathname.split('/')[2]}</title>
                </Helmet>
                <Dialog open={includeCart}>
                    <DialogTitle>Add items in cart? </DialogTitle>
                    <DialogActions className="d-flex justify-content-end">
                        <Button variant="contained" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)' }} size="small" onClick={() => this.buyAll()}>
                            YES
                        </Button>
                        <Button color="secondary" variant="contained" size="small" onClick={() => this.buy()}>
                            NO
                        </Button>
                    </DialogActions>
                </Dialog>
                <Dialog open={productExists}>
                    <DialogTitle>Product is already in cart </DialogTitle>
                    <DialogActions>
                        <Button onClick={this.handleClose} variant="contained" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)' }}>
                            OK
                        </Button>
                    </DialogActions>
                </Dialog>
                <Grid container spacing={3} justify="center" style={{ marginTop: '1vh', width: '99vw' }}>
                    <Grid item xs={12} sm={6}>
                        {product ?
                            <Paper style={{ backgroundColor: '#0e80a9' }}>
                                <Breadcrumbs aria-label="breadcrumb" style={{ padding: '20px' }}>
                                    <Link style={{ color: 'white' }} to="/">
                                        Home
                                    </Link>
                                    <Link style={{ color: 'white' }} to="/products">
                                        Products
                                    </Link>
                                    <Typography style={{ color: 'lightgrey' }}>{this.props.location.pathname.split('/')[2]}</Typography>
                                </Breadcrumbs>
                            </Paper> : null}
                        {product ?
                            <Paper style={{ padding: '20px' }}>
                                <div>
                                    <Typography variant="h5" style={{ color: '#FF8E53' }}>
                                        {product.name}
                                    </Typography>
                                    <br />
                                    <img src={product.image} width="200" alt={product.name}></img>
                                    <Typography variant="h6">
                                        {product.detail}
                                    </Typography>
                                    <hr />
                                    {product.offer !== undefined ?
                                        <Typography color="textSecondary">
                                            {product.info}
                                        </Typography> : null}
                                </div>
                                {product.offer !== undefined ? <p><Chip label={product.offer} color="secondary" /></p> : null}
                                <div style={{ marginBottom: '20px' }}>
                                    <div>
                                        <Button variant="contained" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)', color: 'white', textDecoration: 'none' }} onClick={() => this.checkCart(product)}>
                                            Buy Now
                                        </Button>
                                        <Button variant="outlined" style={{ marginLeft: '20px', borderColor: '#FF8E53', color: '#FF8E53' }} onClick={() => this.addToCart(product)}>
                                            Add to Cart
                                        </Button>
                                    </div>
                                </div>
                            </Paper>
                        : <h1>Product unavailable</h1>}
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        {cart && cart.length ?
                            <Paper className={classes.paper} style={{ backgroundColor: "#0e80a9", color: 'white', textAlign: 'center' }}>
                                <div style={{ padding: '20px' }}>
                                    <h3>Cart</h3>
                                    <hr />
                                    <div style={{ margin: '10px' }}>
                                        {cart.length ? cart.map((product) =>
                                            <Chip key={product.name}
                                                label={product.name}
                                                onDelete={() => this.delete(product)}
                                                style={{ marginRight: '10px', backgroundColor: '#FF8E53', color: 'white' }}
                                            />
                                        ) : null}
                                    </div>
                                    <Link to="/checkout" style={{ textDecoration: 'none' }}>
                                        <Button variant="contained" size="small" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)', color: 'white' }}>
                                            Checkout
                                        </Button>
                                    </Link>
                                </div>
                            </Paper> : null}
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default Details;