import React, { Component } from 'react';
import { Card, CardContent, Button, Paper, Breadcrumbs, Typography, Chip, IconButton } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

class Checkout extends Component {
    state = {
        cart: [],
        checkout: false
    }
    componentDidMount() {
        this.setState({
            cart: JSON.parse(localStorage.getItem("cart"))
        });
        console.log(this.state.cart);
    }

    delete(product) {
        const { cart } = this.state;
        cart.splice(this.findProduct(cart, product), 1);
        this.setState({ cart: cart });
        localStorage.setItem("cart", JSON.stringify(cart));
    }

    findProduct(cart, product) {
        for (let i = 0; i < cart.length; i++) {
            if (cart[i].name === product.name) {
                return i;
            }
        }
        return null;
    }

    checkOut() {
        this.setState({ cart: [], checkout: true });
        localStorage.removeItem("cart");
    }
    render() {
        const { cart } = this.state;
        const { checkout } = this.state;
        return (
            <div style={{ marginTop: "2%" }}>
                <Helmet>
                    <title>Checkout</title>
                </Helmet>
                {cart && cart.length ?
                    <Paper style={{ backgroundColor: '#0e80a9' }}>
                        <Breadcrumbs aria-label="breadcrumb" style={{ padding: '20px' }}>
                            <Link style={{ color: 'white' }} to="/">
                                Home
                            </Link>
                            <Typography style={{ color: 'lightgrey' }}>Checkout</Typography>
                        </Breadcrumbs>
                    </Paper>
                    : null}
                {!checkout ? <div>
                    {cart && cart.length ? <Paper style={{ width: '90vw', minHeight: '70vh' }}>
                        <div style={{ padding: '20px' }}>
                            <ul style={{ marginLeft: '-5.3vh' }}>
                                {cart.map(product =>
                                    <div style={{ padding: '20px', display: 'inline-block' }}>
                                        <Card style={{ backgroundColor: '#FF8E53' }}>
                                            <CardContent>
                                                <div style={{ textAlign: 'end', display: 'block', width: '100%' }}>
                                                    <IconButton onClick={() => this.delete(product)}>
                                                        <CloseIcon style={{ fontSize: '24' }}></CloseIcon>
                                                    </IconButton>
                                                </div>
                                                <div>
                                                    <div>
                                                        <img src={product.image} width="75" height="75" alt={product.name} />
                                                    </div>
                                                    <div style={{ marginLeft: '20px' }}>
                                                        <p style={{ fontSize: '18px' }}>{product.name}</p>
                                                        <p style={{ fontSize: '14px' }}>{product.detail}</p>
                                                        {product.offer ? <Chip color="secondary" label={product.offer} /> : <Chip color="primary" label="NO OFFERS" />}
                                                    </div>
                                                </div>
                                            </CardContent>
                                        </Card>
                                    </div>)}
                            </ul>
                            <Button variant="contained" style={{ background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)', color: 'white' }} onClick={() => this.checkOut()}>Buy Now</Button>
                        </div>
                    </Paper> : <h1>Cart is empty</h1>} </div> :
                    <h1>Item will be delivered within a few days</h1>}
            </div>
        );
    }
}

export default Checkout;