import React from 'react';
import { Component } from 'react';
import './App.css';
import { AppBar, Toolbar, Button } from '@material-ui/core/';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Home from './components/home/Home';
import Products from './components/products/Products';
import Details from './components/details/Details';
import Checkout from './components/checkout/Checkout';
import PageNotFound from './components/pagenotfound/PageNotFound';
import { Helmet } from 'react-helmet';

class App extends Component {
  state = {};

  render() {
    return (
      <div className="app">
        <Helmet>
          <title>Home</title>
        </Helmet>
        <Router>
          <AppBar position="static" style={{ backgroundColor: "#0e80a9", color: 'white' }}>
            <Toolbar>
              <a href="/" className="logo"><h1>eCOM</h1></a>
              <Link to="/products" style={{ textDecoration: 'none' }}>
                <Button variant="contained" color="primary" style={{ marginLeft: '20px', background: 'linear-gradient(45deg, #e3b035 30%, #FF8E53 90%)' }}>
                  Browse
              </Button>
              </Link>
            </Toolbar>
          </AppBar>
          <div className="app-body">
            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/products" exact component={Products} />
              <Route path="/products/:name" component={Details} />
              <Route path="/checkout" component={Checkout} />
              <Route path="/**" component={PageNotFound} />
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

export default App;
